body {

  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0px; 
  padding: 0px;
  background-color: rgba(0, 242, 246, 0.337);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Navbar Component styling */
nav{
  display: flex;
  width: 100%;
  height: 70px;
  background-color: rgb(8, 8, 78);
  color: white;
  justify-content: center;
  align-items: center;
}

/* HeaderComponent styling */
.headerComponent{
  
  background-image: linear-gradient(150deg,#348fb4, #01194b);
  border-radius: 5px;
  padding: 10px;
  min-width: 200px;
  max-width: auto;
  margin: 30px 4px;
}
.headerComponent h2{
  display: flex;
  justify-content: center;
  color: rgb(255, 230, 0);
}

.headerComponent .btnCollection {
  display: flex;
  justify-content: center;
  overflow-x: auto;
}
.headerComponent .btnCollection input{
  width: 45px;
  height: 45px;
  border-radius: 100%;
  border: 2px rgb(76, 249, 237) solid;
  color: black;
  background-color: rgb(255, 255, 255);
  margin: 0px 5px;
}

.headerComponent .btnCollection input:hover{

  color: rgb(255, 255, 255);
  background-color: rgb(239, 0, 192);
 
}
.headerComponent .commentSection{
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.headerComponent .commentSection input{
  margin: 0px 3px;
  height: 40px;
  width: 400px;
  min-width: 30px;
  border-radius: 5px;
  border: 1px rgba(39, 36, 36, 0.633) solid;
}
.headerComponent .commentSection input:hover, .headerComponent .commentSection input:focus {
  color: rgb(0, 0, 0);
  background-color: rgba(148, 237, 249, 0.982);
  border: 1px rgba(39, 36, 36, 0.633) solid;
}
.headerComponent .commentSection button{
  padding: 0px 10px;
  margin-left: 10px;
  background-color: rgb(25, 100, 4);
  color: white;
  border: 2px rgb(36, 35, 35);
  border-radius: 5px;
}
.headerComponent .commentSection button:hover{

  color: rgb(43, 43, 43);
  background-color: rgb(222, 26, 229);
  
}
.ReviewCard{
  display: flex;
  flex-direction: row;
  width: auto;
  padding: 5px;
  border: 2px rgba(86, 86, 34, 0.375) solid;
  background-color: rgb(255, 255, 255);
  color: rgb(2, 18, 136);
  border-radius: 5px;
  min-height: 80px;
  margin: 0px 0px;
  margin-top: 20px;
}
.ReviewCard .rating{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-right: 10px;
  color: white;
  background-color: rgb(99, 0, 118);
  width: 35px;
  height: 35px;
  min-height: 35px;
  min-width: 35px;
  border-radius: 100%;
}
.headerComponent .editContainer{
  margin-top: 20px;
  margin-left: 50px;
  margin-right: 50px;
  display: flex;
  justify-content: space-between;
}
.headerComponent .editContainer button{
  border-radius: 5px;
  background-color: rgb(0, 162, 255);
  color: white;
  border: 2px solid rgba(65, 71, 56, 0.419);
  padding: 5px;
}
.headerComponent .editContainer button:hover{
  background-color: rgb(213, 20, 133);
  color: rgb(252, 255, 252);
}
.headerComponent .aboutLink{
  float: right;
  margin-top: 15px;
}

/* about pagestyling */
.aboutComponent{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 350px;
  margin: 50px 3px 0px 3px;
  background-image: linear-gradient(120deg, rgb(41, 126, 154), rgb(1, 36, 62));
  color: white;
  padding: 20px;
  border-radius: 5px;
  border: 1px rgba(136, 26, 83, 0.162) solid;
}
.aboutComponent h3{
  color: rgb(17, 227, 255);
}

/* welcomepage styling */
.welcomePageComponent{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  margin-left: 10px ;
  margin-right: 10px ;
}
.welcomePageComponent input{
  width: 200px;
  margin-top: 10px;
  height: 30px;
  border-radius: 5px;
  border: 1px solid grey;
  background-color: rgb(255, 255, 255);
}
.welcomePageComponent button{
  background-color: rgb(4, 81, 4);
  color: white;
  border-radius: 5px;
  padding: 5px;
  margin-top: 10px;
}
.welcomePageComponent button:hover{
  background-color: rgb(3, 17, 84);
  color: rgb(250, 252, 255);
}
/* Errorpage styling */
.errorPageComponent{
  display: flex;
  flex-direction: column;
  align-items: center;
}